.projects-page {
  background-color: #DDD4ED;
  padding-top: 1in;
  padding-right: 2in;
  padding-bottom: 1in;
  padding-left: 2in;
  border: 10px solid #443069;
  box-sizing: border-box;
  height: 100%;
}

h1 {
  animation: drop-in 1s ease 100ms backwards;
  font-size: 74px;
  color: #362456;
}

.project-list {
  animation: drop-in 1s ease 100ms backwards;
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr)); /* Update the grid-template-columns */
  gap: 2rem;
}

.project-item {
  background-color: #FFFFFF;
  padding: 2rem;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 10px 10px 0px;
  text-align: center;
}

.project-item img {
  width: 100%;
  max-height: 200px;
  object-fit: cover;
  border-radius: 4px;
}

.project-item h2 {
  font-size: 24px;
  margin-top: 1rem;
  color: #362456;
}

.project-item p {
  font-size: 16px;
  color: #555555;
  margin-top: 0.5rem;
}

@media (max-width: 1180px) { /*big tablet*/
  .projects-page {
    padding: 1in 4rem; /* Adjust the padding for tablets */
    min-height: 100vh;
    min-width: 100vw;
  }

  h1 {
    font-size: 8vw; 
  }

  .project-list {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Adjust the minimum and maximum width of grid items for tablets */
  }

  .project-item h2 {
    font-size: 2.5vw; 
  }

  .project-item p {
    font-size: 2vw;
  }
}


@media (max-width: 768px) {
  .projects-page {
    padding: 1in 1rem; /* Adjust the padding for smaller screens */
    min-height: 100vh;
    min-width: 100vw;
    padding-left: 15%;
    padding-right: 15%;
  }

  h1 {
    font-size: 8vw; /* Adjust the font size for smaller screens */
  }

  .project-list {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr)); /* Adjust the minimum and maximum width of grid items for smaller screens */
  }

  .project-item {
    padding: 1rem;
    max-width: 100vw;
  }

  .project-item h2 {
    font-size: 4vw; /* Adjust the font size for smaller screens */
  }

  .project-item p {
    font-size: 3vw; /* Adjust the font size for smaller screens */
  }
}

@media (max-width: 653px) {
  .projects-page {
    padding: 1in 1rem; /* Adjust the padding for smaller screens */
    min-height: 100vh;
    min-width: 100vw;
    padding-left: 10%;
    padding-right: 10%;
  }

  h1 {
    font-size: 10vw; /* Adjust the font size for smaller screens */
  }

  .project-list {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* Adjust the minimum and maximum width of grid items for smaller screens */
  }

  .project-item {
    padding: 1rem;
    max-width: 100vw;
  }

  .project-item h2 {
    font-size: 4vw; /* Adjust the font size for smaller screens */
  }

  .project-item p {
    font-size: 3vw; /* Adjust the font size for smaller screens */
  }
}