/* Header.css */

* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
  z-index: 999;
}

body {
  font-family: 'Merriweather', serif;
}

nav {
  background: transparent;
  height: 80px;
  width: 100%;
  position: absolute;
  z-index: 9999;
  display: flex;
  justify-content: space-between;
  padding: 0 15%;
}



label.logo {
  font-size: 10px;
  line-height: 80px;
}

.website-logo {
  width: 100px;
  height: auto;
}

nav ul {
  display: flex;
  align-items: center;
}

nav ul li {
  margin: 0 5px;
}

nav ul li a {
  color: #362456;
  font-size: 20px;
  padding: 7px 13px;
  border-radius: 3px;
}

nav ul li:first-child a {
  background: none;
}

.nav-links li a {
  position: relative;
}

.nav-links li a::before {
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #362456;
  transition: width 0.3s ease;
}

.nav-links li a:hover::before {
  width: 100%;
}

.checkbtn {
  font-size: 30px;
  color: white;
  line-height: 80px;
  cursor: pointer;
  display: none;
  transition: color 0.3s; 
}

.checkbtn.white {
  color: white;
}

#check {
  display: none;
}

@media (max-width: 952px) {
  label.logo {
    font-size: 30px;
    padding-left: 50px;
  }

  nav ul li a {
    font-size: 16px;
  }
}

@media (max-width: 858px) {
  .checkbtn {
    display: block;
    z-index: 1000;
    color: #362456;
  }

  ul {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: #362456;
    /* top: 80px; */
    left: -100%;
    text-align: center;
    transition: all 0.5s;
    z-index: 999;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  nav ul li {
    display: block;
    margin: 30px 0;
    line-height: 30px;
  }

  nav ul li a {
    font-size: 20px;
    color: white;
  }

  nav ul li a:hover::before {
    background-color: white;
    height: 1px;
  }

  nav ul li a.active {
    color: white;
  }

  #check:checked ~ ul {
    left: 0;
  }
}

section {
  background-size: cover;
  height: calc(100vh - 80px);
}
